* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(100, 157, 138);
}

.Main {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px 10px;
  color: #f40e0e;
}

.Main h3 {
  font-size: 25px;
}

.Calc-container {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 65vh;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  height: 100%;
}

.row input[type="button"] {
  flex: 1;
  border: 0.5px solid rgb(149, 149, 149);
  color: #000;
  font-weight: 600;
}

.input::-webkit-outer-spin-button,
.input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.Calc-container .input {
  background-color: rgb(161, 153, 153);
  width: 100%;
  border: none;
  outline: none;
  font-size: 27px;
  text-indent: 5px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: right;

  /* text-align: right; */
  padding-right: 5px;
}

.last-row {
  display: flex;
  flex-direction: row;
}

.last-row input[type="button"]:first-child {
  flex: 2;
}

.row .input:first-child {
  background-color: rgb(123, 123, 123);
}

.row input:last-child {
  background-color: orange;
}

.Quotes {
  padding: 5% 20%;
}

.quot {
  display: flex;
  flex-direction: row;
  justify-content: center;
  border: 1px solid black;
}

.box1 {
  width: 20%;
  display: flex;
  align-items: center;
}

.box2 {
  font-size: 18px;
  width: 80%;
}
